<template>
	<view>
		<swiper class="swiper" :circular="true" :indicator-dots="true" :autoplay="true" :interval="3000" :duration="1000">
			<swiper-item v-for="(elem, i) of swipers" :key="i">
				<image class="swiper-image" :src="elem.img" mode=""></image>
			</swiper-item>
		</swiper>
		<!-- 导航区域 -->
		<view class="nav">
			<view class="nav-item" v-for="(elem, i) of navs" :key="i" @click="navItemClick(elem.path)">
				<view :class="elem.icon"></view>
				<text>{{elem.title}}</text>
			</view>
		</view>
		<!-- 推荐商品 -->
		<view class="hot-goods">
			<view class="tit">
				推荐商品
			</view>
			<goods-list :goods="goods" @goDetail="goGoosDetail"></goods-list>
		</view>
	</view>
</template>

<script>
	import goodsList from "../components/goods-list/goods-list.vue"
	export default {
		components:{
			"goods-list": goodsList
		},
		data() {
			return { 
				swipers: [],
				goods: [],
				navs: [
					{
						icon: "iconfont icon-ziyuan",
						title: "黑马超市",
						path: "/pages/goods/goods"
					},
					{
						icon: "iconfont icon-guanyuwomen",
						title: "联系我们",
						path: "/pages/contact/contact"
					},
					{
						icon: "iconfont icon-tupian",
						title: "社区图片",
						path: "/pages/pic/pic"
					},
					{
						icon: "iconfont icon-shipin",
						title: "学习视频",
						path: "/pages/videos/videos"
					}
				]
			}
		},
		onLoad() {
			this.getSwipers();
			this.getHotGoods();
		},
		methods: {
			//  请求轮播图
			getSwipers(){
				this.$myRequist({
					url: "/api/getlunbo",
				}).then(res => {
					this.swipers = res.data.message
				})
			},
			
			// 请求商品列表
			getHotGoods(){
				this.$myRequist({
					url:"/api/getgoods?pageindex=1"
				}).then(res => {
					this.goods =res.data.message
				})
			},
			//导航点击处理函数
			navItemClick(url){
				uni.navigateTo({
					url,
				})
			},
			//跳转商品详情页
			goGoosDetail(id){
				uni.navigateTo({
					url: "/pages/goGoosDetail/goGoosDetail?id="+id
				})
			}
		},
	}
</script>

<style scoped lang="scss">
	.swiper{
		width: 100%;
	}
	.swiper-image{
		width: 100%;
		height: 100%;
	}
	@font-face {font-family: "iconfont";
	  src: url('~@/static/font_nav/iconfont.eot?t=1576844226174'); /* IE9 */
	  src: url('~@/static/font_nav/iconfont.eot?t=1576844226174#iefix') format('embedded-opentype'), /* IE6-IE8 */
	  url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2'),
	  url('~@/static/font_nav/iconfont.woff?t=1576844226174') format('woff'),
	  url('~@/static/font_nav/iconfont.ttf?t=1576844226174') format('truetype'), /* chrome, firefox, opera, Safari, Android, iOS 4.2+ */
	  url('~@/static/font_nav/iconfont.svg?t=1576844226174#iconfont') format('svg'); /* iOS 4.1- */
	}
	
	.iconfont {
	  font-family: "iconfont" !important;
	  font-size: 16px;
	  font-style: normal;
	  -webkit-font-smoothing: antialiased;
	  -moz-osx-font-smoothing: grayscale;
	}
	
	.icon-shipin:before {
	  content: "\f0024";
	}
	
	.icon-tupian:before {
	  content: "\e650";
	}
	
	.icon-guanyuwomen:before {
	  content: "\e608";
	}
	
	.icon-ziyuan:before {
	  content: "\e60d";
	}
	.nav{
		display: flex;
		>.nav-item{
			width: 25%;
			> view{
				background-color: $shop-color;
				text-align: center;
				width: 120rpx;
				height: 120rpx;
				border-radius: 50%;
				line-height: 120rpx;
				margin: 0 auto;
				margin-top: 10px;
				color: #fff;
				font-size: 48rpx;
				
			}
			> text {
				font-size: 30rpx;
				display: block;
				margin: 0 auto;
				margin-top: 10px;
				text-align: center;
			}
		}
	}
	.icon-tupian{
		font-size: 45rpx;
	}
	.hot-goods{
		background-color: #eee;
		overflow: hidden;
		margin-top: 20px;
		.tit{
			height: 50px;
			line-height: 50px;
			color: $shop-color;
			text-align: center;
			letter-spacing: 20px;
			background-color: #fff;
			margin: 7rpx 0px;
		}
	}
</style>
